.article_main {
    width: 900px;
    margin: 100px auto 0;
}
.article_main_img_block-top {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    height: 60px;
    align-items: center;
}
.share {
    grid-column: 4/6;
    justify-self: end;
}
.article-time,
.article-watch {
    display: flex;
    height: 100%;
    align-items: center;
    img {
        margin-right: 15px;
    }
}
.article_main_img_block-center {
    filter: drop-shadow(4px 4px 16px rgba(21, 19, 45, 0.25));
    border-radius: 12px;
    overflow: hidden;
    img {
        width: 100%;
    }
}
.article_main_img_block-bottom {
    display: flex;
    width: max-content;
}

.author_name {
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.blog_author {
    img {
        border-radius: 50%;
        margin-right: 15px;
    }
}
.article_info {
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.about_author_contnent {
    display: flex;
    align-items: center;
}
.article_mark {
    display: grid;
    align-items: center;
    justify-items: center;
    background: #f0f5fc;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 16px;
    padding: 36px 0;
}
.mark_title {
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    display: flex;
    align-items: center;
    color: #242424;
}
.about_author {
    margin-top: 60px;
}
.about_author_title {
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $bark_blue;
}
.stars {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    div {
        margin: 0 5px;
    }
}
.star_active {
    svg {
        fill: #06b25f !important;
    }
}

.about_autor_content_right_side {
    margin-left: 20px;
}
.about_autor_content_right_side_top {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.about_autor_content_right_side_right {
    justify-self: end;
}
.author_name {
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    color: #242424;
    margin: 0;
    display: block;
}

.author_content_text {
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.recent_article_block {
    display: block;
    width: 400px !important;
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 16px;
    overflow: hidden;
}
.article_block_top {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    height: 53px;
    align-items: center;
    > div:first-child {
        grid-column: 1/3;
    }
}
.article_block_author_side {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.article_author {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.article_block_bottom {
    box-sizing: border-box;
    padding: 16px;
}
.block_date {
    color: #242424;
}
.article_author::after {
    right: -10px;
}
.article_block_title {
    font-weight: bold;
    font-size: 24px;
    line-height: 34px;
    text-transform: capitalize;
    color: #242424;
}
