.footer {
    width: 100%;
    position: relative;
    height: 500px;
    background: #194484;
    margin-top: 300px;
    box-sizing: border-box;
    padding: 150px 0 0 0px;
}
.footer_svg {
    position: absolute;
    width: 100%;
    z-index: 1;
    top: 0;
    left: 0;
    svg {
    }
}
.footer_svg_1 {
    width: 102%;
    left: -1%;
}
.footer_svg_2 {
    left: 50%;
    opacity: 0.4;
}
.footer_content {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    width: 1280px;
    margin: 0 auto;
}
.footer_side_1 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.footer_logo {
    grid-column: 1/4;
}
.footer_title {
    font-weight: bold;
    font-size: 20px;
    line-height: 21px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: #ffffff;
}
.footer_side_2 {
    width: max-content;
    margin-left: 100px;
}
.footer-list {
    li {
        margin: 9px 0;
        a {
            display: inline-block;
            width: max-content;
            font-size: 16px;
            line-height: 20px;
            color: #ffffff;
            text-decoration: none;
        }
    }
}
.footer_container {
    display: grid;
    height: 100%;
}
.footer_foot {
    background: #fff;
    align-self: flex-end;
    height: 60px;
    display: flex;
    align-items: center;
}
.footer_foot_content {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    width: 1280px;
    margin: 0 auto;
    a {
        font-family: "Open Sans";
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #4c4c4c;
    }
    > a:nth-child(1) {
        grid-column: 1/4;
    }
    > a:not(:first-child) {
        justify-self: center;
    }
}
