.slick-slider {
	position: relative;

	.slick-track,
	.slick-list {
		-webkit-transform: translate3d(0, 0, 0);
		-moz-transform: translate3d(0, 0, 0);
		-ms-transform: translate3d(0, 0, 0);
		-o-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
	}

	.slick-list {
		position: relative;
		overflow: hidden;
		width: 100%;
	}

	.slick-track {
		position: relative;
		display: flex;
	}

	.slick-slide {
		position: relative;
	}

	.slick-prev,
	.slick-next {
		display: none;
		text-indent: -10000px;
		position: absolute;
		top: 50%;
		width: 18px;
		height: 26px;
		background-repeat: no-repeat;
		background-color: transparent;
		background-position: center;
		background-size: 100%;
		transition: all 0.2s ease-out;
		z-index: 1;
	}

	.slick-prev {
		// background-image: url("../img/icons/right-arrow-white_lg.svg");
		transform: translateY(-50%);
	}

	.slick-next {
		// background-image: url("../img/icons/right-arrow-white_lg.svg");
		transform: translateY(-50%) rotate(180deg);
	}

	.slick-dots {
		display: flex;
		position: absolute;
		top: 701px;
		left: 150px;
		transform: translateX(-50%);

		li {
			margin: 0 5px;

			button {
				text-indent: -9999px;
				width: 20px;
				height: 20px;
				border-radius: 50%;
				border: 3px solid #fff;
				background: none;
			}

			&.slick-active {
				button {
					background: #fff;
				}
			}
		}
	}
}
