.join_team_main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 50px;
}
.accordion-title {
    width: 1130px;
    height: 100px;
    background: #ffffff;
    box-shadow: 0px 3px 16px rgba(29, 51, 107, 0.15);
    border-radius: 12px;
    box-sizing: border-box;
    align-items: center;
    padding-left: 80px;
    margin: 50px 0;
    position: relative;
    transition: all 0.5s ease-in-out;
    &::after {
        content: "";
        display: block;
        position: absolute;
        left: 30px;
        width: 12px;
        top: 30px;
        height: 12px;
        border-bottom: 6px solid $bark_blue;
        border-right: 6px solid $bark_blue;
        transform: rotate(45deg);
        transition: all 0.5s ease-in-out;
    }
}
.accordion-title_text {
    display: inline-flex;
    height: 100%;
    align-items: center;
    width: max-content;
}

.accordion-content {
    max-height: 0;
    transition: all 0.5s ease-in;
    transition: max-height 1.5s ease-in;
    overflow: hidden;
}
.acordion-title-active {
    height: max-content;
    padding-bottom: 20px;
    .accordion-title_text {
        margin-top: 30px;
    }
    &::after {
        transform: rotate(-135deg);
    }
    .accordion-content {
        max-height: 2000px;
    }
    &-modal {
        height: max-content;
        padding-bottom: 20px;
        .accordion-title_text {
            margin-top: 30px;
        }
        &::after {
            transform: rotate(-135deg);
        }
        .accordion-content {
            max-height: 2000px;
        }
    }
}
.accordion-content {
    padding-left: 0;
    margin-left: -30px;
    max-width: 900px;
}
.content-title {
    list-style-image: url(../img/join_team_dot.svg) !important;
}
.accordion-title_text {
    font-family: "Harabara";
    font-weight: bold;
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $bark_blue;
}
.apply_button {
    position: absolute;
    right: 50px;
    background: #06b25f;
    border-radius: 6px;
    top: 30px;
    width: 160px;
    height: 42px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    text-transform: uppercase;

    color: #ffffff;
}
.content-title_text {
    font-family: "Harabara";
    font-weight: bold;
    font-size: 20px;
    line-height: 38px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $bark_blue;
    margin: 5px 0 5px -30px;
}
.content-text {
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.current_opening-title {
    font-family: "Harabara";
    font-weight: bold;
    font-size: 36px;
    line-height: 39px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    text-align: center;
    color: $bark_blue;
}
.modal_window {
    width: 480px;
    height: 580px;
    background: #fff;
    &-title {
        font-weight: bold;
        font-size: 24px;
        line-height: 38px;
        letter-spacing: 0.05em;
        color: #194484;
        text-align: center;
        margin: 5px 0;
    }
    &-2nd_title {
        font-weight: 500;
        font-size: 18px;
        line-height: 38px;
        text-align: center;
        text-transform: capitalize;
        color: #242424;
        margin: 5px 0;
    }
    &-inputs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 400px;
        margin: 0 auto;
        input[type="text"],
        textarea {
            margin: 10px 0;
            grid-column: 1/3;
            border: 1px solid #d7d7d7;
            box-sizing: border-box;
            border-radius: 4px;
            &::placeholder {
                font-size: 16px;
                line-height: 26px;
                color: #a9a9a9;
            }
        }
        input,
        textarea,
        label {
            padding: 7px 16px;
            box-sizing: border-box;
            font-size: 16px;
            line-height: 26px;
            color: #a9a9a9;
        }
        input[type="text"] {
            height: 40px;
        }
        textarea {
            height: 80px;
        }
        label {
            border: 1px solid #d7d7d7;
            box-sizing: border-box;
            border-radius: 4px;
            width: 250px;
            display: inline-block;
            height: 40px;
            overflow: hidden;
        }
        input[type="file"] {
            display: none;
        }
    }
    &-button {
        font-weight: bold;
        font-size: 16px;
        line-height: 36px;
        display: grid;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #06b25f;
        background: #fff;
        border: 2px solid #06b25f;
        box-sizing: border-box;
        border-radius: 6px;
        width: 100%;
    }
}
.send {
    background: #2da531;
    border-radius: 6px;
    display: grid;
    align-items: center;
    text-align: center;
    height: 50px;
    grid-column: 1/3;
    color: #fff;
    font-weight: bold;
    font-size: 16px;
    line-height: 36px;
    text-align: center;
    text-transform: uppercase;
}
.current_opening {
    position: relative;
}
.modal_window {
    position: absolute;
    display: none;
    left: 30%;
    top: 35%;
}
.modal_active {
    display: block;
}
.apply_button {
    z-index: 1;
}
