.index_wrapper .header {
	background: rgba(33, 33, 33, 0.3);
	position: absolute;
	z-index: 1;
	* {
		color: #ffffff;
		font-weight: bold;
	}
}
.header {
	box-shadow: 0px 4px 12px rgba(24, 9, 96, 0.1);
	height: 80px;
	width: 100%;
	* {
		color: $dark_blue;
		font-weight: bold;
	}
}
.close {
	display: none;
}
.index_wrapper .header_container {
	width: 1300px;
}
.header_container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 100%;
	width: 1360px;
}
.burger_menu {
	display: none;
}
.nav {
	display: grid;
	grid-template-columns: repeat(6, auto);
	width: 683px;
	padding: 0;
	margin: 0;
	height: 100%;
	align-items: center;
	box-sizing: border-box;
	> li {
		height: 100%;
		display: flex;
		align-items: center;
		cursor: pointer;
		justify-content: center;
		position: relative;
	}
}

.index_wrapper .header_button {
	border: 2px solid #ffffff;
	color: #fff;
}
.header_button {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 160px;
	height: 42px;
	border: 2px solid $green_;
	color: $green_;
	box-sizing: border-box;
	border-radius: 6px;
}
.arrow {
	position: relative;
}
.arrow::after {
	content: "";
	display: inline-block;
	position: absolute;
	width: 6px;
	height: 6px;
	border: 1px solid $dark_blue;
	border-top: transparent;
	border-right: transparent;
	right: 0;
	top: 43%;
	transform: rotate(-45deg);
}
.index_wrapper .arrow::after {
	border: 1px solid #ffffff;
	border-top: transparent;
	border-right: transparent;
}
.index_wrapper {
	.nav_content {
		background: rgba(33, 33, 33, 0.8);
	}
}
.nav_content {
	display: block;
	transform: scaleY(0);
	transition: all 400ms ease-in;
	transform-origin: 0 0;
	position: absolute;
	top: 80%;
	left: 0;
	background: white;
	box-shadow: 2px 4px 16px rgba(158, 158, 158, 0.3);
	width: 344px;
	border-top: 3px solid #00a4e8;
	border-radius: 0 0 16px 16px;
	padding: 12px 0 12px 16px;
	> li:not(:first-child) {
		margin-top: 10px;
	}

	> li:hover {
		a {
			color: $active_color;
		}
	}
}

.nav_active {
	> span {
		color: $active_color;
	}
	&::after {
		border-color: $active_color;
	}
	.nav_content {
		transform: scale(100%);
	}
}
