.contact_us-inputs {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 500px;
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 6px;
    box-sizing: border-box;
    padding: 40px 40px 60px 31px;
    .send {
        grid-column: 1/2;
    }
    input {
        height: 40px;
    }
    input,
    textarea {
        margin: 20px 0;
        box-sizing: border-box;
        padding: 10px 16px;
        background: #ffffff;
        border: 1px solid #d7d7d7;
        box-sizing: border-box;
        border-radius: 4px;
        font-size: 14px;
        line-height: 19px;
        color: #a9a9a9;
        &::placeholder {
            font-size: 14px;
            line-height: 19px;
            color: #a9a9a9;
        }
    }
    textarea {
        height: 80px;
    }
}
.input_text {
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    color: #242424;
}
.contact-us {
    .main_content_left-side {
        max-width: 570px;
    }
}
.contact_text {
    text-align: left;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    color: #242424;
}
.contact_info_wave {
    margin-top: -200px;
}
.main_content {
    z-index: 2;
}
.contact_info_wave-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 1200px;
    margin: 0 auto;
    a {
        z-index: 1;
        margin: 20px 0;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: center;
        text-transform: capitalize;
        color: #242424;
        img {
            margin-right: 20px;
        }
    }
}
.contact_info_wave-side {
    display: grid;
}
.contact-us {
    .footer {
        margin: 0;
    }
}
.nav_content {
    z-index: 10;
}
