@media all and (max-width: 420px) {
    .accordion-content {
        margin-left: -60px !important;
        width: 95%;
    }
    .article_main_img_block-top {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
        align-items: center;
        height: auto !important;
        > div {
            margin: 10px 0;
        }
    }
    .img-left {
        width: 100% !important;
        height: 100% !important;
    }
    .author_text {
        display: block;
        margin-top: 25px;
    }
    .author-name {
        font-size: 26px;
        margin: 10px auto !important;
    }
    .share {
        grid-column: 1/2 !important;
    }
    .min {
        display: none;
    }
    .article_block_title {
        font-size: 20px !important;
    }
    .article_block_author_side {
        margin: 0;
    }
    .article_block_author_side {
        * {
            margin: 5px 0;
        }
    }
    .article_block_center {
        img {
            width: 99%;
        }
    }
    .path {
        display: none;
    }
    .recent_project_block {
        margin: 0 10px;
        img {
            width: 100% !important;
            height: 100% !important;
        }
    }
    .accordion_title {
        margin-left: 50px;
        position: relative;
    }
    .accordion_title::before {
        position: absolute;
        margin-right: 0px !important;
        left: -30px !important;
        z-index: 1;
        width: 10px !important;
        height: 10px !important;
    }
    .header_container {
        justify-content: start;
    }
    .burger_menu::after {
        width: 40px;
        height: 5px;
        border: 3px solid$bark_blue;
        border-left: transparent;
        border-right: transparent;
        top: 21px;
        right: 30px;
    }
    .burger_menu::before {
        width: 40px;
        height: 3px;
        top: 39px;
        right: 30px;
    }
    .second_slide {
        .slider_title {
            font-size: 18px;
        }
        .green_title {
            display: none;
        }
    }

    .first_svg {
        bottom: -2px;
        svg {
            width: 150% !important;
            height: 60px !important;
        }
    }
    .slick-dots {
        display: none !important;
    }
    .slider-content {
        width: 300px;
        top: 8%;
        left: 5%;
    }
    .slider_title {
        font-size: 24px;
        margin-bottom: 0;
    }
    .button {
        width: 142px;
        height: 40px;
    }
    .buttons {
        grid-template-columns: repeat(1, 1fr) !important;
        .button:nth-child(1) {
            margin-bottom: 10px;
        }
    }
    .tehnologies_icon {
        width: 100%;
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
        > svg:nth-child(3) {
            grid-column: 1/3;
        }
    }
    .tehnologies_blocks {
        > svg:nth-child(2) {
            width: 80%;
        }
    }
    .looking {
        svg {
            height: 80px !important;
        }
    }
    .looking_title {
        font-size: 28px;
    }

    .looking_text {
        margin-top: 0;
        font-size: 16px;
        text-align: center;
    }
    .contact {
    }
    .strong_text {
        font-size: 20px;
    }
    .contact_text {
        font-size: 14px;
    }
    .contact_block_left_Side {
        a {
            font-size: 16px;
        }
    }
    .contact_inputs_block {
        width: 90%;
    }
    .footer_side_1 {
        justify-items: center;
        grid-template-columns: repeat(2, 1fr) !important;
        a {
            margin: 10px 20px;
        }
        a:last-child {
            grid-column: 1/3;
        }
    }
    .footer_logo {
        grid-column: 1/3;
    }
    .footer_content {
        margin: 0 !important;
        width: 100%;
    }
}
@media all and (max-width: 450px) {
    .after-border::after {
        display: none !important;
    }
    .blog_block {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        width: 90%;
    }
    .blog_block_left-side {
        img {
            width: 100% !important;
            height: 100% !important;
        }
    }
    .blog_block_right-side-top {
        grid-template-columns: repeat(1, 1fr) !important;
        > div {
            margin: 10px 0;
        }
    }
    .blog_title {
        font-size: 20px !important;
    }
    .blog_text {
        font-size: 14px !important;
    }
    .blog_block_right-side-watch {
        grid-column: 1/2 !important;
    }
    .blog_block-right-side-bottom {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        width: 100% !important;
    }
    .after-border::after {
        width: 160% !important;
        top: 160% !important;
        left: -50% !important;
        height: 2px !important;
    }
}
