@media all and (min-width: 1000px) and (max-width: 1250px) {
    .header {
        .b_button {
            display: none;
        }
    }
    .last_nav_content {
        width: max-content;
        padding-right: 20px;
        left: -30%;
    }
    .first_svg {
        bottom: -2px;
    }

    .footer_content {
        grid-template-columns: repeat(3, 1fr);
    }

    .footer_side_5 {
        grid-column: 3/4;
    }
}
@media all and (max-width: 1150px) {
    .about_us_block {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
    }
    .portfolio_blocks {
        width: 90% !important;
        margin: 0 auto;
        display: flex !important;
        justify-items: center;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        grid-template-columns: repeat(2, 1fr);
    }
    .order-2 {
        order: 0 !important;
    }
    .portfolio_block {
        width: 400px;
        grid-template-columns: repeat(1, 1fr) !important;
        img {
            width: auto !important;
            height: auto !important;
        }
    }
}
@media all and (max-width: 1250px) {
    .accordion-title {
        width: 90% !important;
        margin-left: auto !important;
        margin-right: auto !important;
    }
    .current_opening-accordion {
        padding-left: 0px;
    }
    .footer {
        height: auto;
    }
    .service {
        width: 100%;
        img {
            width: 100%;
        }
    }
    .looking_content {
        width: 100%;
    }

    .looking_title {
        text-align: center;
    }
    .main_content {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .vr_header_block {
        width: 95% !important;
    }
    .faqs {
        width: 100% !important;
    }
    .accordion {
        margin: 60px auto 0;
    }
    .products_blocks {
        justify-content: center !important;
    }
    .product_block {
        margin: 15px !important;
    }
}
@media all and(max-width:1250px) and (min-width: 600px) {
    .faqs_main_block {
        width: 80% !important;
    }
    .accordion {
        width: 100% !important;
        padding-left: 0;
    }
}
@media all and (max-width: 1300px) and (min-width: 1100px) {
    .partners_blocks {
        grid-template-columns: repeat(3, 1fr);
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .partner_block {
        margin: 15px 0;
    }
}
@media all and (max-width: 1100px) and (min-width: 800px) {
    .partners_blocks {
        grid-template-columns: repeat(2, 1fr);
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        justify-items: center;
    }
    .partners {
        .slick-list {
            grid-column: 1/3;
        }
        .slick-track {
        }
    }
    .partner_block {
        margin: 15px 0;
        justify-content: center;
        width: 90% !important;
        display: grid !important;
        align-items: center;
    }
    .partners {
        .slick-slide {
            > div {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                align-items: center;
                justify-content: center;
            }
        }
    }
    .blog_blocks {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .blog_block {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
    }
    .blog_block_right-side {
        padding: 0 !important;
    }
    .blog_block_right-side-top {
        margin-top: 15px;
        display: grid !important;
        width: 100% !important;
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
    .blog_block_right-side-watch {
        margin-top: 15px;
        grid-column: 1/3;
    }
    .blog_block_left-side {
        img {
            width: 100%;
            height: 100%;
        }
    }
    .blog_block-right-side-content {
        * {
            width: 90% !important;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .blog_author {
        margin-left: 10px;
    }
}
