@media all and (min-width: 1250px) and (max-width: 1400px) {
    .slide {
        img {
            height: 70% !important;
        }
    }
    .slider-content {
        top: 10%;
        .green_title {
            margin: 0;
        }
    }
    .slider_title {
        font-size: 38px;
        margin-bottom: 0;
    }
    .first_svg {
        top: 60%;
        width: 100%;
        svg {
            width: 100% !important;
            height: 90px !important;
        }
    }
    .slick-dots {
        top: 60% !important;
        left: 50% !important;
    }
}
@media all and (max-width: 1400px) {
    .blog_block {
        width: 90%;
        margin-left: auto !important;
        margin-right: auto !important;
    }
    .nav_content {
        width: max-content;
        padding-right: 20px;
    }
    .container_big {
        width: 100%;
    }
    .header_container {
        width: 100% !important;
    }
    .achievements {
        width: 100%;
    }
    .achievements_blocks {
        grid-template-columns: repeat(3, 1fr);
        height: auto;
    }
    .achievements_block {
        min-height: 400px;
        margin-top: 20px;
        &:nth-child(4) {
            grid-column: 2/3;
        }
    }
    .container_small {
        width: 100%;
    }
    .contacts {
        width: 100%;
    }
    .contact_block {
        // grid-template-columns: repeat(1, 1fr);
        margin-top: 50px;
    }
    .contact_block_left_Side {
        // justify-self: center;
        a {
            margin: 10px 0;
            max-width: 90%;
        }
    }
    .right_side {
        img {
            display: none;
        }
    }
    .footer_content {
        width: 90%;
    }
    .footer_foot {
        width: 100%;
    }
    .footer_foot_content {
        width: 100%;
    }
    .footer_svg {
        top: -1%;
    }
    .soc {
        display: none;
    }
}
