@media all and (max-width: 500px) and (min-width: 420px) {
    .slider_title {
        font-size: 24px !important;
        width: 300px;
        margin-bottom: 0;
    }
    .first_svg {
        bottom: -2px;
        svg {
            width: 215% !important;
            height: 65px !important;
        }
    }
    .burger_menu::after {
        width: 40px;
        height: 5px;
        border: 3px solid $bark_blue;
        border-left: transparent;
        border-right: transparent;
        top: 21px;
    }
    .burger_menu::before {
        width: 40px;
        height: 3px;
        top: 39px;
    }
    .slider-content {
        width: 350px;
        top: 10%;
        left: 10%;
    }
    .slider_title {
        font-size: 30px;
        margin-bottom: 0;
    }
    .button {
        width: 142px;
        height: 40px;
    }
    .slick-dots {
        display: none !important;
    }

    .burger_active {
        .nav {
            width: 80%;
        }
        .close {
            left: 70%;
        }
    }
    .looking {
        svg {
            height: 90px !important;
        }
    }
    .looking_title {
        font-size: 34px;
    }
    .looking_text {
        font-size: 18px;
        text-align: center;
        margin: 0;
    }
    .footer_side_1 {
        a:not(:first-child) {
            svg {
                width: 90%;
                height: 70%;
            }
        }
    }
}
@media all and (max-width: 600px) {
    .contact_us-inputs {
        width: 90% !important;
        margin: 0 auto;
    }
    .contact_us-inputs {
        padding: 10px !important;
    }
    .modal_window {
        left: 0% !important;
        top: 35%;
    }
    .team_block {
        &-title {
            font-size: 18px !important;
            line-height: 38px !important;
        }
    }
    .about_text {
        width: 100% !important;
    }
    .about_us_left_side {
        img {
            width: 100%;
        }
    }
    .about_title {
        font-size: 24px !important  ;
        line-height: 38px !important;
    }
    .img-left {
        float: none !important;
        margin: 20px auto !important;
        justify-self: center;
    }
    .author_page {
        display: grid;
    }

    .faqs {
        width: 100% !important;
    }
    .faqs_main_block {
        width: 70% !important;
    }
    .accordion {
        width: 100% !important;
        padding-left: 0;
    }
    .faqs_main_block {
        a {
            font-size: 16px !important;
        }
    }
    .accordion_title {
        font-size: 16px !important;
    }
    .accordion_text {
        font-size: 14px !important;
    }
    .article_main_img_block-top {
        grid-template-columns: repeat(2, 1fr) !important;
        justify-items: center;
        align-items: center;
    }
    .share {
        justify-self: center !important;
    }
    .about_author_contnent {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .about_autor_content_right_side {
        margin-top: 20px;
    }
    .about_author_title,
    .mark_title {
        text-align: center;
    }
}
@media all and (max-width: 600px) and (min-width: 420px) {
    .article_main_img_block-top {
        grid-template-columns: repeat(2, 1fr) !important;
        justify-items: center;
        align-items: center;
    }
    .share {
        grid-column: 2/3 !important;
    }
}
@media all and (max-width: 500px) {
    .portfolio_block {
        width: 90% !important;
        img {
            width: 100% !important;
        }
    }
    .modal_window {
        width: 100% !important;
        // left: 0% !important;
        // top: 35%;
        label,
        .modal_window-button {
            grid-column: 1/3;
        }
    }
    .modal_window-inputs {
        width: 90% !important;
    }
    .article_main_img_block-bottom,
    .article_block_author_side {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        align-items: center;
        margin: 20px auto;
    }
    .after-border::after {
        top: 120% !important;
        left: -30%;
        width: 140% !important;
        height: 1px !important;
    }
    .top-svg {
        svg {
            height: 60px !important;
            width: 180% !important;
        }
    }
    .left_border {
        left: -10px !important;
    }
    .right_border {
        right: -10px !important;
    }
    .main_content_title {
        font-size: 24px;
        text-align: center;
    }
    .logo {
        margin: 0;
    }
    .home {
        display: none;
    }
    .footer_side_1 {
        a:not(:first-child) {
            svg {
                width: 90%;
                height: 70%;
            }
        }
    }
    .footer_svg {
        svg {
            height: 60px !important;
        }
    }
    .footer_svg_1 {
        svg {
            width: 250% !important;
        }
    }
    .footer_svg_2 {
        left: 20%;
        top: 2px;
        svg {
            width: 450% !important;
        }
    }
    .footer_foot {
        height: auto;
    }
    .footer_foot_content {
        grid-template-columns: repeat(1, 1fr) !important;
        a:first-child {
            grid-column: 1/2;
            justify-self: center;
        }
        a {
            margin: 10px 0;
        }
    }
    .footer_title {
        font-size: 16px;
    }
    .footer-list {
        a {
            font-size: 14px !important;
        }
    }
    .header {
        justify-content: start;
        height: 60px;
    }
    .header_container {
        justify-content: start;
        height: 60px;
    }
    .first_svg {
        top: 39% !important;
    }
    .second_slide {
        width: 400px;
        .title {
            width: 100%;
        }
        .green_title {
            display: none;
        }
    }
    .slide {
        img {
            height: 350px !important;
        }
    }
    .looking_first_svg {
        svg {
            width: 191% !important;
            height: 70px !important;
        }
    }
    .partners_blocks {
        width: 80% !important;
        margin: 0 auto;
        grid-template-columns: repeat(1, 1fr) !important;
    }
    .partners {
        padding: 120px 0;
    }
    .partner_second_svg,
    .partner_first_svg {
        svg {
            width: 151% !important;
            height: 50px !important;
        }
    }
    .ser_block {
        width: 145px !important;
        height: 162px !important;
        img {
            width: 40%;
        }
    }
    .ser_text {
        text-align: center;
        font-size: 12px;
    }
}
@media all and (max-width: 600px) and (min-width: 500px) {
    .slide {
        img {
            height: 350px !important;
        }
    }
    .slider-content {
        top: 10% !important;
    }
    .first_svg {
        top: 35% !important;
    }
}
@media all and (max-width: 800px) {
    .main_content_title {
        text-align: center;
    }
    .not-img {
        .main_content {
            height: auto;
        }
    }
    .burger_active {
        .nav {
            width: 80%;
            padding-left: 10px;
            height: 100%;
            overflow: auto;
            > li {
                height: max-content;
                width: 90%;
            }
        }
        .nav_content {
            width: auto;
        }
        .close {
            left: 70%;
            font-size: 20px;
        }
    }
    .tehnologies {
        margin: 100px auto 0;
    }
    .tehnologies_blocks {
        grid-template-columns: repeat(1, 1fr);
        height: auto;
        width: 80%;
        margin: 0 auto;
        padding-bottom: 20px;
        * {
            margin-top: 20px;
        }
    }
    .servise_blocks {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .servise_block:nth-child(3) {
        order: 2;
    }
    .servise_block:nth-child(4) {
        order: 3;
    }
    .servise_block:nth-child(7) {
        order: 7;
    }
    .achievements_blocks {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .achievements_block:nth-child(4) {
        grid-column: 1/2;
    }
    .achievements_block:nth-child(3),
    .achievements_block:nth-child(4) {
        img {
            left: 20%;
            top: 10%;
        }
    }
    .contact_block {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .contact_block {
        .right_side {
            margin-top: 50px;
        }
    }
    .footer_content {
        grid-template-columns: repeat(1, 1fr);
        div {
            margin: 10px 0;
            width: 310px;
        }
    }
    .footer_logo {
        margin: 15px 0 50px !important;
        grid-column: 1/6;
    }
    .footer_side_1 {
        justify-items: center;
        grid-template-columns: repeat(5, 1fr);
        width: 100% !important;
        a {
            margin: 10px 20px;
        }
    }
    .main_content {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .vr_header {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
        width: 80%;
        margin: 0 auto;
    }
    .main_content_title {
        margin-top: 70px;
        width: 100%;
    }
    .header_title {
        text-align: center;
    }
    .main_content_rigth-side {
        img {
            width: 100%;
        }
    }
}
@media all and (min-width: 500px) and (max-width: 800px) {
    .footer_logo {
        margin: 15px 0 50px !important;
        grid-column: 1/6;
        svg {
            height: 90px;
            width: 350px;
        }
    }
    .header_container {
        justify-content: start;
    }
    .burger_active {
        .nav {
            width: 80%;
        }
        .close {
            left: 70%;
        }
    }
    .first_svg {
        top: 51%;
        svg {
            width: 275% !important;
            height: 90px !important;
        }
    }
    .slick-dots {
        display: none !important;
    }
    .slider-content {
        width: 370px;
        top: 15%;
        left: 10%;
    }
    .slider_title {
        font-size: 32px;
        margin-bottom: 0;
    }
    .slide {
        img {
            height: 450px;
        }
    }
    .second_slide {
        width: 510px;
        .title {
        }
        .green_title {
            display: none;
        }
    }
    .button {
        width: 146px;
        height: 42px;
    }
    .looking {
        svg {
            height: 100px !important;
        }
    }
    .looking_title {
        font-size: 36px;
    }
    .looking_text {
        font-size: 20px;
        text-align: center;
    }
    .partners_blocks {
        grid-template-columns: repeat(2, 1fr);
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        justify-items: center;
    }
    .partners {
        .slick-list {
            grid-column: 1/3;
        }
        .slick-track {
        }
    }
    .partner_block {
        margin: 15px 0;
        justify-content: center;
        width: 90% !important;
        display: grid !important;
        align-items: center;
    }
    .partners {
        .slick-slide {
            > div {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
                align-items: center;
                justify-content: center;
            }
        }
    }
    .partner_second_svg,
    .partner_first_svg {
        svg {
            width: 121% !important;
        }
    }
}
@media all and (max-width: 650px) {
    .apply_button {
        display: none !important;
    }
    .current_opening-title {
        font-size: 22px !important;
        line-height: 24px !important;
    }
    .accordion_title-text {
        font-size: 20px !important;
        line-height: 38px !important;
    }

    .accordion-content {
        margin-left: -60px !important;
        width: 95%;
    }
    .acordion-title-active {
        .apply_button {
            display: block !important;
            top: auto !important;
            bottom: 50px !important;
            width: 90% !important;
            left: 5%;
        }
        padding-bottom: 100px !important;
    }
}
@media all and (max-width: 800px) and (min-width: 600px) {
    .modal_window {
        left: 15% !important;
        top: 35%;
    }
}
