.vr_header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
}
.vr_header_block {
    width: 553px;
}
.main_content {
    position: relative;
}

.header_title {
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.05em;
    color: $dark_blue;
}
.vr-app-tehnologies_blocks {
    max-width: 900px;
    margin: 100px auto 0;
}
.vr_app_services {
    margin-top: 200px;
}
.vr_app_servives_blocks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.ser_block {
    background: #f4f8fd;
    box-shadow: 0px 3px 16px 4px rgba(29, 51, 107, 0.1);
    border-radius: 16px;
    overflow: hidden;
    display: grid;
    justify-items: center;
    align-items: center;
    padding: 50px 0 30px;
    height: 320px;
    box-sizing: border-box;
    width: 320px;
    flex-shrink: 0;
    margin: 0 0px 60px;
    * {
        transition: all 0.2s ease-in;
    }
}
.ser_block:hover {
    background: #fff;
    * {
        transform: scale(1.1);
    }
}
.ser_text {
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $dark_blue;
}
@media all and (max-width: 500px) {
    .ser_text {
        font-size: 12px;
        line-height: 15px;
    }
    .vr_app_services_block {
        padding-top: 20px;
    }
}
.vr_app-contacts {
    margin-top: 180px;
}
.recent_project {
    position: relative;
}
.recent_project_blocks,
.recent_articles_blocks {
    .slick-prev {
        background-image: url("../img/arrow-left.svg") !important;
        display: inline-block !important;
        transform: translateY(-50%);
        width: 30px;
        height: 45px;
        left: -50px;
    }
    .slick-next {
        display: inline-block !important;
        background-image: url("../img/arrow-right.svg") !important;
        transform: rotate(0deg);
        right: -50px;
        width: 30px;
        height: 45px;
    }
    .slick-track {
        justify-content: space-between;
    }
}
.recent_project_block {
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 16px;
    width: 400px !important;
    overflow: hidden;
    box-sizing: border-box;
    display: grid;
    align-items: center;
    img {
        width: 100%;
    }
}
.recent_text {
    font-size: 16px;
    line-height: 26px;
    color: #000000;
    width: 90%;
    justify-self: center;
    margin-top: 0;
}
.recent_title {
    margin: 0 auto 8px;
    width: 95%;
}
.faqs {
    margin: 70px auto 0;
    width: 1016px;
}
.faqs_main_block {
    list-style: url("../img/dot.svg");
    width: 870px;
    margin: 0 auto;
    position: relative;
    background: #fff;
    li {
        margin: 13px 0;
    }
    a {
        font-weight: 600;
        font-size: 20px;
        line-height: 30px;
        align-items: center;
        color: $dark_blue;
    }
}
.b_border {
    border: 6px solid #66d6a0;
    border-radius: 8px;
    position: absolute;
    z-index: 0;
    width: 90px;
    box-sizing: border-box;
    height: 90%;
    top: 10px;
    z-index: -1;
}
.left_border {
    left: -45px;
}
.right_border {
    right: -45px;
}
.accordion {
    margin-top: 60px;
    width: 900px;
}
.accordion_text {
    font-size: 16px;
    line-height: 26px;
    color: $dark-gray;
}
.accordion_item {
    margin: 10px 0;
    padding-bottom: 10px;
}
.accordion_title {
    display: inline-block;
    font-size: 20px;
    line-height: 30px;
    text-transform: capitalize;
    color: #242424;
    font-weight: bold;
    position: relative;
    display: flex;
    align-items: center;
}
.accordion_title::before {
    // position: absolute;
    margin-right: 28px;
    content: "";
    width: 12px;
    height: 12px;
    box-sizing: border-box;
    border: 2px solid black;
    border-top: transparent;
    border-right: transparent;
    transform: rotate(-45deg);
    left: 0;
    transition: all 0.5s ease-in-out;
}
.accordion_content {
    // transform: scaleY(0);
    max-height: 0;
    overflow: hidden;
    transition: all 0.8s ease-in;
    transform-origin: 0 0;
}
.accordion_active {
    border-bottom: 1px solid $dark_blue;
    .accordion_content {
        max-height: 10000px;
        // transform: scaleY(100%);
    }
    .accordion_title::before {
        transform: rotate(135deg);
    }
}
