.img-left {
    float: left;
    margin: 0 20px 20px 0;
    width: 320px;
    height: 340px;
}
.author_page_top {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 10px;
    p {
        text-align: left;
        margin: 0;
    }
}
.author_soc {
    justify-self: end;
    img {
        margin: 0 10px;
    }
}
.prof {
    font-weight: bold;
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.05em;
    color: #242424;
    margin: 12px 0 30px;
}
.author_text {
    line-height: 26px;
    color: #242424;
}
