// Подключение своих SCSS файлов
$active_color: #44c9ff;
$dark_blue: #0d6eb7;
$green_: #06b25f;
$light_green: #66d6a0;
$dark-gray: #242424;
$bark_blue: #194484;
$blue: #00a4e8;
$med_blue: #276ed8;
.wrapper {
    width: 1600px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    min-height: 1500px;
}
main {
    position: relative;
}
.soc {
    position: absolute;
    right: 0;
    top: 350px;
    background: #fff;
    z-index: 1;
    display: grid;
    height: 192px;
    width: 48px;
    a {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #fff;
    }
}
.container {
    margin: 0 auto;
}
ul {
    list-style: none;
}
a {
    text-decoration: auto;
}
button {
    border: none;
    cursor: pointer;
}
body {
    font-family: "Montserrat";
}
.container_small {
    width: 1200px;
    margin: 0 auto;
}
.container_big {
    width: 1360px;
    margin: 0 auto;
}
main {
    min-height: 1000px;
}
.slide {
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 960px;
    }
}

.first_svg {
    position: absolute;
    bottom: 0px;
    left: 0px;
}
.slick-arrow {
    display: none !important;
}
.title {
    font-family: "Harabara";
    font-style: normal;
    font-weight: bold;
    text-transform: uppercase;
}
.slider-content {
    position: absolute;
    top: 180px;
    left: 128px;
    width: 551px;
}
.slider_title {
    color: #fff;
    font-size: 44px;
    line-height: 60px;
}
.slider_text {
    font-size: 16px;
    line-height: 26px;
    color: #ffffff;
}
.button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 42px;
    color: #fff;
    cursor: pointer;
}
.second_slide {
    width: 735px;
}
.green_title {
    color: $light_green;
    font-size: 29px;
    line-height: 38px;
}
.transparent_button {
    border: 2px solid #ffffff;
    box-sizing: border-box;
    border-radius: 6px;
}
.green_button {
    background: $green_;
    border-radius: 6px;
}
.buttons {
    width: 344px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.tehnologies {
    width: 900px;
    margin: 180px auto 0;
}
.tehnologies_title {
    font-size: 36px;
    line-height: 39px;
    letter-spacing: 0.1em;
    color: $bark_blue;
    text-align: center;
}
.tehnologies_text {
    font-size: 24px;
    line-height: 38px;
    text-align: center;
}
.blue {
    font-weight: bold;
    color: $blue;
}
.dark_blue {
    font-weight: bold;
    color: $dark_blue;
}
.tehnologies_icon {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 470px;
    margin: 0 auto;
}
.tehnologies_blocks {
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 16px;
    height: 138px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
    .button {
        width: 220px;
        text-transform: uppercase;
    }
}
.service {
    width: 1130px;
    margin: 120px auto 0;
}
.servise_blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.blue_title {
    font-weight: bold;
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $dark_blue;
}
.servise_block_text {
    font-size: 16px;
    line-height: 26px;
    color: $dark-gray;
    font-weight: 400;
}
.servise_block {
    width: 95%;
}
.looking {
    width: 100%;
    background: $med_blue;
    height: 540px;
    margin-top: 100px;
    position: relative;
    display: grid;
    justify-items: center;
    align-items: center;
}
.looking_content {
    width: 1100px;
    height: 182px;
    display: grid;
    justify-items: center;
}
.looking_title {
    font-size: 44px;
    line-height: 60px;
    color: #ffffff;
    margin: 0;
    letter-spacing: 0.06em;
}
.looking_text {
    font-size: 24px;
    line-height: 29px;
    color: #ffffff;
}
.looking_first_svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.looking_second_svg {
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
}
.white_button {
    border: 2px solid $green_;
    box-sizing: border-box;
    border-radius: 6px;
    background: #fff;
    color: $green_;
}
.achievements {
    margin-top: 100px;
}
.achievements_blocks {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    height: 400px;
}
.achievements_block {
    position: relative;
    width: 322px;
    background: rgba(39, 110, 216, 0.05);
    border-radius: 16px;
    display: grid;
    box-sizing: border-box;
    padding: 250px 25px 0;
    img {
        position: absolute;
        left: 0;
        top: 0;
    }
    &:nth-child(3),
    &:nth-child(4) {
        img {
            left: 20%;
            top: 10%;
        }
    }
}
.achievements_text {
    font-size: 16px;
    line-height: 26px;

    text-align: center;
}
.partners {
    background: rgba(39, 110, 216, 0.04);
    box-sizing: border-box;
    padding: 220px 0 300px;
    margin-top: 130px;
    position: relative;
}
.partner_second_svg,
.partner_first_svg {
    position: absolute;
    width: 100%;
}
.partner_second_svg {
    bottom: 0px;
    left: 0;
}
.partner_first_svg {
    top: 0;
    left: 0;
}
.partners_blocks {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
.partner_block {
    width: 322px;
    height: 160px;
    background: #ffffff;
    box-shadow: 0px 3px 16px 4px rgba(29, 51, 107, 0.1);
    border-radius: 16px;
    display: grid;
    justify-items: center;
    align-items: center;
    margin: 5px 0;
}
.strong_text {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: $dark-gray;
}
.contact_text {
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: $dark-gray;
}
.contact_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 100px;
}
.contact_block_left_Side {
    display: grid;
    a {
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: center;
        text-transform: capitalize;
        color: $dark-gray;
        justify-content: space-between;
        width: max-content;
        span {
            display: inline-block;
            margin-left: 20px;
        }
    }
}
.right_side {
    position: relative;
    img {
        top: -52px;
        left: -60px;
        position: absolute;
        z-index: -1;
        height: 464px;
    }
}
.contact_inputs_block {
    display: grid;
    width: 420px;
    height: 400px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 50px 40px 70px;
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.4);
    border-radius: 6px;
    z-index: 1;
    input {
        margin-bottom: 20px;
        height: 40px;
        padding: 10px 16px;
        box-sizing: border-box;
        background: #ffffff;
        border: 1px solid #d7d7d7;
        border-radius: 4px;
    }
    textarea {
        height: 80px;
        padding: 10px 16px;
        box-sizing: border-box;
        background: #ffffff;
        border: 1px solid #d7d7d7;
        border-radius: 4px;
    }
    input::placeholder,
    textarea::placeholder,
    input,
    textarea {
        font-family: "Open Sans";
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #a9a9a9;
    }
    button {
        margin-top: 30px;
        width: 100%;
        color: #fff;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        color: #ffffff;
    }
}
.container_big {
    position: relative;
}
.main_content {
    margin-top: 43px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}
.main_content_title {
    font-size: 44px;
    line-height: 60px;
    letter-spacing: 0.06em;
    color: $bark_blue;
}
.path {
    position: absolute;
    top: 0px;
    left: 0;
    display: flex;
    justify-content: space-between;
    padding: 0;
    align-items: center;
    li {
        margin: 0 10px;
        position: relative;
    }
    a {
        font-size: 16px;
        line-height: 26px;
        color: $bark_blue;
    }
}
.path-arrow::after {
    display: inline-block;
    content: "";
    width: 5px;
    height: 5px;
    position: absolute;
    border: 1px solid #194484;
    border-top: transparent;
    border-right: transparent;
    transform: rotate(-135deg);
    top: 40%;
    left: 110%;
}

.nav_content {
    z-index: 1;
}
.not-img {
    .main_content {
        height: 470px;
    }
}
.vr-active {
    .service-site {
        position: relative;
        span {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}

.blog-active {
    .blog-site {
        position: relative;
        a {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}
.contact-active {
    .contact-site {
        position: relative;
        span {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}
.tehno-active {
    .tehnologies-site {
        position: relative;
        span {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}
.products-active {
    .products-site {
        position: relative;
        span,
        a {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}
.portfolio-active {
    .portfolio-site {
        position: relative;
        span,
        a {
            color: $active_color;
        }
        &::before {
            width: 100%;
            content: "";
            display: block;
            height: 5px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: $active_color;
        }
    }
}
.burger_active {
    .nav {
        > li::before {
            background: none;
        }
    }
}
.main_block {
    display: grid;
    align-items: center;
    justify-items: center;
    margin-top: 50px;
    img {
        width: 100%;
    }
}
.block {
    display: block;
    margin: 10px 0;
}
.main_content {
    z-index: 0 !important;
}
.portfolio-active,
.products-active {
    .footer {
        margin: 300px 0 0 !important;
    }
}
@import "fonts";
@import "header";
@import "slick";
@import "footer";
@import "adaptive";
@import "vr_app";
@import "products";
@import "portfolio";
@import "blog";
@import "article";
@import "author_page";
@import "about_us";
@import "team";
@import "join_team";
@import "contact_us";
