.portfolio_blocks {
    width: 1130px;
    margin: 0 auto;
}
.portfolio_block {
    margin: 70px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background: #fff;
    box-shadow: 0px 3px 16px rgba(29, 51, 107, 0.15);
    border-radius: 12px;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
    }
}
.portfolio_block_right_side {
    justify-self: center;
    align-self: center;
    p {
        margin-left: auto;
        margin-right: auto;
    }
}
.order-2 {
    order: 1;
}
