.about_title {
    font-size: 38px;
    line-height: 50px;
}
.about_us_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}
.about_text {
    width: 550px;
    font-size: 16px;
    line-height: 26px;
    color: #000000;
}
.about_text {
    &-blue {
        color: $blue;
    }
    &-block {
        display: block;
        font-size: 24px;
        line-height: 34px;
        margin: 5px 0;
    }
    &-bold {
        font-weight: bold;
    }
}
