.team {
    grid-template-columns: repeat(1, 1fr);
}
.team_title {
    text-align: center;
    margin-top: 100px;
}
.team_blocks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.team_main {
    margin: 0 auto;
}
.team_block {
    background: #ffffff;
    box-shadow: 0px 3px 16px 2px rgba(29, 51, 107, 0.15);
    border-radius: 16px;
    width: 325px;
    height: 515px;
    margin: 30px 0;
    overflow: hidden;
    img {
        width: 100%;
    }
    &-title,
    &-text {
        margin: 0 0 0 15px;
    }
    &-title {
        font-weight: bold;
        font-size: 24px;
        line-height: 38px;
        text-transform: uppercase;
        color: $dark_blue;
        margin-bottom: 8px;
    }
    &-text {
        font-size: 16px;
        line-height: 26px;
        color: #242424;
    }
}
.team_main {
    max-width: 1135px;
}
