.wave {
    box-sizing: border-box;
    padding: 200px 0;
    background: rgba(132, 181, 255, 0.1);
    position: relative;
}
.products_blocks {
    display: flex;
    flex-wrap: wrap;

    justify-content: space-between;
}
.product_block {
    position: relative;
    width: 400px;
    margin: 15px 0;
}
.wave-active {
    .looking {
        margin: -100px 0 0 0;
        z-index: -1;
    }
}
.top-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
