.blog_block {
    display: flex;
    background: #ffffff;
    box-shadow: 0px 3px 16px rgba(29, 51, 107, 0.15);
    border-radius: 12px;
    margin: 30px 0;
}
.blog_block_right-side-top {
    display: flex;
    width: 60%;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    div {
        display: flex;
        align-items: center;
        span {
            margin-left: 10px;
        }
    }
}
.blog_author {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: max-content;
    img {
        margin-right: 30px;
    }
}
.blog_block-right-side-bottom {
    display: flex;
    width: max-content;
    align-items: center;
}
.blog_block_left-side {
    img {
        height: 100%;
    }
}
.blog_date {
    margin-left: 40px;
}
.blog_block_right-side {
    box-sizing: border-box;
    padding: 20px 40px 20px 100px;
}
.blog_block_right-side-top {
    span {
        font-size: 16px;
        line-height: 26px;
        color: #242424;
    }
}
.blog_title {
    font-weight: bold;
    font-size: 24px;
    line-height: 26px;
    text-transform: capitalize;
    color: #242424;
}
.blog_text {
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}
.blog_block-right-side-bottom {
    p {
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        color: #242424;
    }
}
.after-border {
    position: relative;
}
.after-border::after {
    display: block;
    content: "";
    width: 1px;
    height: 24px;
    background: #b1b1b1;
    right: -20px;
    top: 0;
    position: absolute;
}
.blog-active {
    .blog {
        padding-bottom: 300px;
    }

    .footer_svg_1 {
        svg {
            fill: rgb(231, 237, 245) !important;
        }
    }
}
.wave-active {
    .footer {
        margin-top: -250px;
    }
}
