@media all and (min-width: 800px) and (max-width: 1000px) {
    .header {
        .b_button {
            display: none;
        }
    }
    .nav {
        display: none;
    }
    .header_container {
        justify-content: start;
    }

    .container {
        width: 90%;
    }

    .looking {
        svg {
            height: 100px !important;
        }
    }

    .achievements_blocks {
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
    .footer_content {
        grid-template-columns: repeat(2, 1fr);
    }

    .footer_side_5 {
        grid-column: 1/3;
    }
}
@media all and (max-width: 1000px) {
    .looking_first_svg {
        top: -5px;
    }
    .looking_second_svg {
        bottom: -10px;
    }
    .header {
        height: 80px;
    }
    .header_container {
        height: 80px;
    }
    .header_container {
        // z-index: -1;
    }
    .burger_menu {
        z-index: 1;
        display: block;
    }
    .burger_menu::after {
        content: "";
        display: block;
        position: absolute;
        right: 50px;
        top: 30px;
        border: 4px solid $bark_blue;
        width: 58px;
        height: 10px;
        border-left: transparent;
        border-right: transparent;
        transition: all 0.6s ease-in-out;
        z-index: 1;
    }
    .index_wrapper {
        .burger_menu::after {
            border: 4px solid #ffffff;
            border-left: transparent;
            border-right: transparent;
        }
        .burger_menu::before {
            background: #ffffff;
        }
    }
    .burger_menu::before {
        z-index: 1;
        content: "";
        display: block;
        position: absolute;
        right: 50px;
        width: 58px;
        height: 4px;
        background: $bark_blue;
        top: 58px;
        transition: all 0.6s ease-in-out;
    }
    .nav {
        position: fixed;
        // transform: scaleX(0);
        left: -100%;
        transition: all 0.5s ease-in;
        display: block;
        transform-origin: 0 0;
        width: 100%;
    }
    .burger_active {
        .nav {
            // transform: scaleX(100%);
            box-shadow: 12px 0px 12px rgba(6, 2, 29, 0.6);
            // height: max-content;
            height: 100%;
            left: 0;
            top: 0;
            background: #ffffff;
            padding-left: 50px;
            width: 50%;
            padding-top: 100px;
            z-index: 1;
            * {
                display: block;
                width: 100%;
                padding-right: 0;
                color: $dark_blue;
            }
            > li {
                margin: 5px 0;
                height: max-content;
            }
        }
        .close {
            display: inline-block;
            position: absolute;
            color: $green_;
            left: 80%;
            font-size: 25px;
            top: 25px;
            width: max-content;
        }
        .nav_content {
            transition: all 0s;
            position: static;
            background: none;
            box-shadow: none;
            width: 100%;
            border: none;
            padding-left: 20px;
            transform: scale(100%);
            li {
                margin-top: 10px;
            }
        }
        .b_button {
            display: flex;
            z-index: 1;
            border-color: #06b25f;
            color: #06b25f;
            width: 160px;
            margin-bottom: 100%;
        }
    }
    .slide {
        img {
            width: 100%;
            height: auto;
        }
    }
    .first_svg {
        svg {
            width: 250% !important;
            height: 150px !important;
        }
    }
    .slick-dots {
        top: 57% !important;
    }
    .slider-content {
        top: 15%;
    }
    .logo {
        margin-left: 50px;
    }
}
@media all and (max-width: 1000px) {
    .arrow::after {
        display: none !important;
    }

    .join_team_main {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
    }
    .contact_info_wave-container {
        grid-template-columns: repeat(1, 1fr) !important;
        width: 90% !important;
    }
    .slider_text {
        // display: none;
        position: absolute;
        color: $dark_gray;
        z-index: 1;
        text-align: center;
    }
    .team_blocks {
        justify-content: center !important;
    }
    .team_block {
        margin-left: auto !important;
        margin-right: auto !important;
    }
    .article_main {
        width: 90% !important;
    }

    .first_svg {
        top: 60%;
    }
    .slide {
        height: 750px;
        img {
            height: 590px;
        }
    }
    .tehnologies {
        width: 100%;
    }
    .blog_blocks {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .blog_block {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        width: 420px;
    }
    .blog_block_right-side {
        padding: 0 !important;
    }

    .blog_block_right-side-top {
        margin-top: 15px;
        display: grid !important;
        width: 100% !important;
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
    .blog_block_right-side-watch {
        margin-top: 15px;
        grid-column: 1/3;
    }
    .blog_block_left-side {
        img {
            width: 100%;
            height: 100%;
        }
    }
    .blog_block-right-side-content {
        * {
            width: 90% !important;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .blog_author {
        margin-left: 10px;
    }
}
@media all and (max-width: 900px) {
    .recent_project_block {
        margin: 0 auto;
    }
    .portfolio_blocks {
        justify-content: center;
    }
    .recent_article_block {
        margin: 0 auto;
    }
    .author_page_top {
        grid-template-columns: repeat(1, 1fr) !important;
        width: max-content;
    }
    .author_page_top {
        margin: 0 auto;
    }
    .author_soc {
        justify-self: center !important;
    }
}
